<template>
  <div class="drawer">
    <v-navigation-drawer
      location="right"
      temporary
      v-model="drawer"
      :width="windowWidth <= 767 ? windowWidth / 2 : 370"
      class="pr-1"
    >
      <v-card class="px-0" elevation="0">
        <v-card-title
          class="pl-0 pr-2 d-flex justify-space-between align-center"
          style="font-size: 17px; font-weight: bold"
          >Shopping Cart
          <v-icon size="22" @click="drawer = false"
            >mdi-close</v-icon
          ></v-card-title
        >
        <v-card-text class="px-0 py-0" style="color: #6f6f6f"
          >{{ cartItems.length }} Items</v-card-text
        >
        <v-card-text
          class="px-0"
          style="color: #6f6f6f"
          v-if="!cartItems.length"
          >Free shipping for all orders over $10000.00</v-card-text
        >
        <v-card-text
          class="px-0 text-center"
          style="color: #6f6f6f"
          v-if="!cartItems.length"
          >Your cart is empty</v-card-text
        >
        <div
          class="bar-parent mt-4 position-relative mr-2"
          v-if="cartItems.length"
        >
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="shipping-fast"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 640 512"
            class="svg-inline--fa fa-shipping-fast fa-w-20 fa-3x"
            width="30"
            :style="`position: absolute;
              bottom: 50%;
              z-index: 1;
              left: calc(${
                parseInt((calcTotalPrice / 10000) * 100) <= 100
                  ? (calcTotalPrice / 10000) * 100
                  : 100
              }% - 30px);
              transition: 0.15s all ease-in-out;`"
          >
            <path
              :fill="
                parseInt((calcTotalPrice / 10000) * 100) < 50
                  ? '#F44336'
                  : parseInt((calcTotalPrice / 10000) * 100) > 50 &&
                    parseInt((calcTotalPrice / 10000) * 100) < 100
                  ? '#ff9800'
                  : '#4CAF50'
              "
              d="M624 352h-16V243.9c0-12.7-5.1-24.9-14.1-33.9L494 110.1c-9-9-21.2-14.1-33.9-14.1H416V48c0-26.5-21.5-48-48-48H112C85.5 0 64 21.5 64 48v48H8c-4.4 0-8 3.6-8 8v16c0 4.4 3.6 8 8 8h272c4.4 0 8 3.6 8 8v16c0 4.4-3.6 8-8 8H40c-4.4 0-8 3.6-8 8v16c0 4.4 3.6 8 8 8h208c4.4 0 8 3.6 8 8v16c0 4.4-3.6 8-8 8H8c-4.4 0-8 3.6-8 8v16c0 4.4 3.6 8 8 8h208c4.4 0 8 3.6 8 8v16c0 4.4-3.6 8-8 8H64v128c0 53 43 96 96 96s96-43 96-96h128c0 53 43 96 96 96s96-43 96-96h48c8.8 0 16-7.2 16-16v-32c0-8.8-7.2-16-16-16zM160 464c-26.5 0-48-21.5-48-48s21.5-48 48-48 48 21.5 48 48-21.5 48-48 48zm320 0c-26.5 0-48-21.5-48-48s21.5-48 48-48 48 21.5 48 48-21.5 48-48 48zm80-208H416V144h44.1l99.9 99.9V256z"
              class=""
            ></path>
          </svg>
          <v-progress-linear
            :color="
              parseInt((calcTotalPrice / 10000) * 100) < 50
                ? 'red'
                : parseInt((calcTotalPrice / 10000) * 100) > 50 &&
                  parseInt((calcTotalPrice / 10000) * 100) < 100
                ? 'orange'
                : 'green'
            "
            height="9"
            :model-value="
              parseInt((calcTotalPrice / 10000) * 100) <= 100
                ? (calcTotalPrice / 10000) * 100
                : 100
            "
            striped
          ></v-progress-linear>
        </div>
        <v-card-text
          class="px-0 pt-2"
          style="color: #6f6f6f"
          v-if="cartItems.length && 10000 - calcTotalPrice > 0"
          >Only ${{ 10000 - calcTotalPrice }} away from Free
          Shipping</v-card-text
        >
        <v-card-text
          class="px-0 pt-2"
          style="color: #6f6f6f"
          v-if="cartItems.length && 10000 - calcTotalPrice <= 0"
          >Your order now is included Free Shipping</v-card-text
        >
        <v-card-actions v-if="!cartItems.length">
          <v-btn
            style="
              text-transform: none;
              border-radius: 30px;
              border-color: rgb(199, 199, 199);
            "
            class="w-100"
            variant="outlined"
            density="compact"
            height="45"
            @click="drawer = false"
            >Continue Shopping</v-btn
          >
        </v-card-actions>
      </v-card>
      <v-card
        class="pa-0 mt-5 items-cart"
        elevation="0"
        v-if="cartItems.length"
        max-height="200"
        style="overflow-y: auto"
      >
        <v-container class="px-1">
          <v-row
            v-for="item in cartItems"
            :key="item.id"
            class="align-center mb-4"
          >
            <v-col cols="12" sm="5"
              ><img :src="item.thumbnail" class="w-100" alt=""
            /></v-col>
            <v-col cols="12" sm="7">
              <v-card-title
                class="px-0"
                style="white-space: pre-wrap; font-size: 14px; line-height: 1.2"
                >{{ item.ttitle }} Sample - {{ item.category }}
              </v-card-title>
              <v-card-text class="px-0 pb-0" style="color: #6f6f6f">
                Category: {{ item.category }}
              </v-card-text>
              <v-card-text
                class="px-0 pt-2 font-weight-bold"
                style="color: #6f6f6f"
              >
                ${{
                  Math.ceil(
                    item.price - item.price * (item.discountPercentage / 100)
                  ) * item.quantity
                }}
              </v-card-text>
              <div
                class="item-footer d-flex justify-space-between align-center"
              >
                <div
                  class="counter px-1"
                  style="
                    border-radius: 30px;
                    border: 1px solid rgb(202, 202, 202);
                    width: fit-content;
                  "
                >
                  <v-icon
                    color="#6f6f6f"
                    size="19"
                    @click="item.quantity > 1 ? item.quantity-- : false"
                    >mdi-minus</v-icon
                  >
                  <input
                    type="number"
                    style="
                      border: none;
                      outline: none;
                      width: 35px;
                      font-size: 12 px;
                      color: #6f6f6f;
                    "
                    class="text-center py-3"
                    min="1"
                    v-model="item.quantity"
                  />
                  <v-icon color="#6f6f6f" size="19" @click="item.quantity++"
                    >mdi-plus</v-icon
                  >
                </div>
                <v-icon size="22" @click="deleteItem(item.id)"
                  >mdi-close</v-icon
                >
              </div>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
      <v-card class="pa-0 mt-6" elevation="0" v-if="cartItems.length">
        <v-card-actions
          style="gap: 15px"
          class="flex-column justify-center align-center"
        >
          <v-btn
            style="
              text-transform: none;
              border-radius: 30px;
              border-color: rgb(199, 199, 199);
            "
            variant="elevated"
            density="compact"
            elevation="0"
            class="w-100"
            height="45"
            color="blue"
            @click="toCheckout"
            >Check Out</v-btn
          >
          <v-btn
            style="
              text-transform: none;
              border-radius: 30px;
              border-color: rgb(199, 199, 199);
            "
            variant="outlined"
            density="compact"
            class="w-100 mx-0"
            height="45"
            @click="$router.push({ name: 'cart_page' })"
            >View Cart</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { cartStore } from "@/stores/cart";
import { mapActions, mapState } from "pinia";

export default {
  inject: ["Emitter"],
  props: {
    windowWidth: {
      type: Number,
    },
  },
  data: () => ({
    drawer: false,
  }),
  computed: {
    ...mapState(cartStore, ["cartItems"]),
    calcTotalPrice() {
      let total = 0;
      this.cartItems.forEach((product) => {
        total +=
          Math.ceil(
            product.price - product.price * (product.discountPercentage / 100)
          ) * product.quantity;
      });
      return total;
    },
  },
  methods: {
    ...mapActions(cartStore, [
      "getCartItems",
      "deleteItem",
      "setToLocalStorage",
    ]),
    toCheckout() {
      this.setToLocalStorage();
      this.$router.push({ name: "check_out" });
    },
  },
  mounted() {
    this.Emitter.on("openCart", () => {
      this.drawer = true;
    });
  },
};
</script>

